@import "../../../styles/mixins";
.buttonBar {
	display: grid;
	grid-template-columns: 1fr 2fr 6fr;
	align-items: center;
	margin: 10px 13px;

	.buttonAdd {
		@include bluButton(160px);
	}

	.searchInput {
		@include alignToFlex(null, center, center);

		& img {
			width: 15px;
			height: 15px;
		}
	}
	.addLesson {
		@include bluButton(160px);
	}
}
