.divInputSpan {
	position: relative;
	display: flex;
	flex-direction: column;
	margin: 5px;
	.superInput {
		border-radius: 5px;
		max-width: 250px;

		&:focus {
			outline: #3eb1e5;
			border-color: #3eb1e5;
			border-radius: 5px;
		}
	}
}
.errorInput {
	outline: red;
	border-color: red;
	border-radius: 5px;
	max-width: 250px;
	min-width: 100px;
}
.errorSpan {
	position: absolute;
	top: 20px;
	color: red;
	width: 200px;
}
