@import "../../styles/mixins";

.table {
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	border: black 1px solid;
	border-radius: 10px;
	background: rgba(0, 0, 0, 0.7);
	color: white;
	height: 700px;
	width: 90%;
	margin: 15px 0;

	.bodyTable {
		margin: 0 10px 10px 10px;
		padding: 5px;
		display: flex;
		flex-direction: column;
		border-radius: 0 0 10px 10px;
		background: rgba(0, 120, 255, 0.3);
		overflow: auto;

		.rowTable {
			display: grid;
			grid-template-columns: 2% 18% 18% 15% 41% 6%;
			grid-template-rows: 25px;
			align-items: center;
			justify-items: start;
			min-height: 45px;

			.buttonRemove {
				@include alignToFlex(20px, center, center);
				background-color: darkred;
				color: white;
				height: 20px;
				border: #1976d2 3px solid;
				border-radius: 50%;
				font-size: 10px;
				&:focus {
					color: red;
				}

				&:hover {
					border: #1976d2 2px dotted;
				}
			}

			.firstNameBody {
				padding-left: 5px;
			}

			.gradesBody {
				@include alignToFlex(100%, space-between, null);
				.oneGrade {
				}
			}

			.finalAssessmentBody {
				@include alignToFlex(100%, center, null);
				color: #149270;
			}
			.finalAssessmentRed {
				@include alignToFlex(100%, center, null);
				color: #831111;
			}
		}
	}
}
